"use client";
import React, { useState } from "react";
import MaxWidthWrapper from "@/components/MaxWidthWrapper";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogFooter,
} from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Check, Plus, Settings2 } from 'lucide-react';
import AddExpenseDialog from "@/components/AddExpenseDialog";
import SettleUpDialog from "@/components/SettleUpDialog";
import RecentActivityDialog from "@/components/RecentActivityDialog";
import GroupBalancesDialog from "@/components/GroupBalancesDialog";
import AddUserDialog from "@/components/AddUserDialog";

type User = {
  id: number;
  name: string;
  balance: string;
};

type Expense = {
  id: number;
  amount: string;
  description: string;
  date: string;
  paidBy: User;
  splitBetween: User[];
};

type Group = {
  id: number;
  name: string;
  users: User[];
  expenses: Expense[];
};

export default function Page() {
  const [groups, setGroups] = useState<Group[]>([
    { id: 1, name: "Trip to Paris", users: [], expenses: [] },
    { id: 2, name: "Weekend Hangout", users: [], expenses: [] },
  ]);
  const [selectedGroup, setSelectedGroup] = useState(groups[0]);
  const [groupBalances, setGroupBalances] = useState([
    { id: 1, name: "John Doe", balance: "$0" },
    { id: 2, name: "Jane Smith", balance: "$25" },
  ]);
  const [isAddingGroup, setIsAddingGroup] = useState(false);
  const [newGroupName, setNewGroupName] = useState("");
  const [isAddExpenseDialogOpen, setIsAddExpenseDialogOpen] = useState(false);
  const [isSettleUpDialogOpen, setIsSettleUpDialogOpen] = useState(false);
  const [isRecentActivityDialogOpen, setIsRecentActivityDialogOpen] = useState(false);
  const [isGroupBalancesDialogOpen, setIsGroupBalancesDialogOpen] = useState(false);
  const [isAddUserDialogOpen, setIsAddUserDialogOpen] = useState(false);
  
  const handleGroupSelect = (group: Group) => {
    setSelectedGroup(group);
  };

  const handleAddExpense = () => {
    setIsAddExpenseDialogOpen(true);
  };

  const handleSettleUp = () => {
    setIsSettleUpDialogOpen(true);
  };

  const handleRecentActivity = () => {
    setIsRecentActivityDialogOpen(true);
  };

  const handleGroupBalances = () => {
    setIsGroupBalancesDialogOpen(true);
  };

  const handleAddUser = () => {
    setIsAddUserDialogOpen(true);
  };

  function handleAddGroup(event: React.MouseEvent<HTMLButtonElement, MouseEvent>): void {
    event.preventDefault();
    if (newGroupName.trim() === "") {
      alert("Group name cannot be empty.");
      return;
    }
    const newGroup: Group = { id: groups.length + 1, name: newGroupName, users: [], expenses: [] };
    setGroups([...groups, newGroup]);
    setNewGroupName("");
    setIsAddingGroup(false);
  }

  return (
    <div className="min-h-[calc(100vh-64px)] bg-gray-50 dark:bg-inherit">
      <MaxWidthWrapper className="min-w-[60vw]">
        <div className="h-[calc(100vh-64px)] grid place-content-center">
          <Card className="max-w-5xl w-full h-[70vh] border-2 border-green-600 dark:border-rose-800 rounded-xl shadow-lg overflow-scroll">
            <div className="grid h-full md:grid-cols-[250px_1fr_250px]">
              {/* Left Sidebar */}
              <div className="border-r dark:border-gray-800 p-4 space-y-4">
                <nav className="space-y-2">
                  <Button variant="ghost" className="w-full justify-start">
                    Dashboard
                  </Button>
                  <Button variant="ghost" className="w-full justify-start" onClick={handleRecentActivity}>
                    Recent Activity
                  </Button>
                </nav>
                <div className="pt-4 border-t dark:border-gray-800 min-h-96 max-h-96 overflow-x-hidden overflow-y-scroll">
                  <h3 className="text-sm font-medium mb-2">GROUPS</h3>
                  {groups.map((group) => (
                    <Button
                      key={group.id}
                      variant="ghost"
                      className={`w-full justify-start ${
                        group.id === selectedGroup.id
                          ? "text-green-600 dark:text-rose-600 font-bold"
                          : "text-gray-700 dark:text-gray-400"
                      }`}
                      onClick={() => handleGroupSelect(group)}
                    >
                      {group.name}
                    </Button>
                  ))}
                  {isAddingGroup ? (
                    <div className="mt-2 flex flex-col items-start justify-normal space-y-2">
                      <Input
                        type="text"
                        value={newGroupName}
                        onChange={(e) => setNewGroupName(e.target.value)}
                        placeholder="Group name"
                        className="ml-1 flex-1 p-1 max-w-52 border focus:ring-green-800 dark:focus:border-rose-800 border-green-600 rounded dark:bg-gray-800 dark:ring-rose-700"
                      />
                      <div className="flex space-x-2">
                      <Button
                        onClick={handleAddGroup}
                        className="bg-green-600 hover:bg-green-700 dark:bg-rose-600 dark:hover:bg-rose-700"
                      >
                        Save
                      </Button>
                      <Button
                        variant="ghost"
                        onClick={() => {
                          setIsAddingGroup(false);
                          setNewGroupName("");
                        }}
                      >
                        Cancel
                      </Button>
                        </div>
                    </div>
                  ) : (
                    <Button
                      variant="ghost"
                      className="w-full justify-start text-green-600 dark:text-rose-600 mt-2"
                      onClick={() => setIsAddingGroup(true)}
                    >
                      <Plus className="w-4 h-4 mr-2" />
                      Add Group
                    </Button>
                  )}
                </div>
                <div className="pt-4 border-t dark:border-gray-800">
                  <Button
                    variant="ghost"
                    className="w-full justify-start text-green-600 dark:text-rose-600"
                    onClick={handleAddUser}
                  >
                    <Plus className="w-4 h-4 mr-2" />
                    Add User
                  </Button>
                </div>
              </div>

              {/* Main Content */}
              <div className="p-6 flex flex-col items-center justify-center text-center">
                <h1 className="text-xl font-bold mb-4">{selectedGroup.name}</h1>
                <div className="rounded-full bg-green-50 dark:bg-rose-900/20 p-6 mb-4">
                  <Check className="w-12 h-12 text-green-600 dark:text-rose-400" />
                </div>
                <h2 className="text-2xl font-semibold mb-2">Payments to Make</h2>
                <p className="text-gray-600 dark:text-gray-400">
                  {selectedGroup.expenses.length === 0
                    ? "You have no pending expenses in this group."
                    : "Here are your pending payments:"}
                </p>
                {selectedGroup.expenses.map((expense) => (
                  <div key={expense.id} className="mt-2">
                    <p>{`${expense.description}: ${expense.amount}`}</p>
                  </div>
                ))}
                <div className="mt-6 space-x-4">
                  <Button
                    className="bg-green-600 hover:bg-green-700 dark:bg-rose-600 dark:hover:bg-rose-700"
                    onClick={handleAddExpense}
                  >
                    Add an expense
                  </Button>
                  <Button variant="outline" onClick={handleSettleUp}>
                    Settle up
                  </Button>
                </div>
              </div>

              {/* Right Sidebar */}
              <div className="border-l dark:border-gray-800 p-4">
                <div className="flex items-center justify-between mb-4">
                  <h3 className="text-sm font-medium">GROUP BALANCES</h3>
                  <Button
                    variant="ghost"
                    size="icon"
                    onClick={handleGroupBalances}
                  >
                    <Settings2 className="w-4 h-4" />
                  </Button>
                </div>
                <div className="space-y-4 hidden md:block">
                  {groupBalances.map((balance) => (
                    <div key={balance.id} className="flex items-center gap-3">
                      <div className="w-8 h-8 rounded-full bg-gray-200 dark:bg-gray-800" />
                      <div className="flex-1">
                        <p className="text-sm font-medium">{balance.name}</p>
                        <p className="text-xs text-gray-600 dark:text-gray-400">
                          {balance.balance}
                        </p>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </Card>
        </div>
      </MaxWidthWrapper>

      <AddExpenseDialog
        isOpen={isAddExpenseDialogOpen}
        onClose={() => setIsAddExpenseDialogOpen(false)}
        onAddExpense={(expense) => {
          setSelectedGroup({
            ...selectedGroup,
            expenses: [...selectedGroup.expenses, expense],
          });
          setIsAddExpenseDialogOpen(false);
        }}
      />

      <SettleUpDialog
        isOpen={isSettleUpDialogOpen}
        onClose={() => setIsSettleUpDialogOpen(false)}
        groupBalances={groupBalances}
      />

      <RecentActivityDialog
        isOpen={isRecentActivityDialogOpen}
        onClose={() => setIsRecentActivityDialogOpen(false)}
        expenses={selectedGroup.expenses}
      />

      <GroupBalancesDialog
        isOpen={isGroupBalancesDialogOpen}
        onClose={() => setIsGroupBalancesDialogOpen(false)}
        groupBalances={groupBalances}
      />

      <AddUserDialog
        isOpen={isAddUserDialogOpen}
        onClose={() => setIsAddUserDialogOpen(false)}
        onAddUser={(user) => {
          setSelectedGroup({
            ...selectedGroup,
            users: [...selectedGroup.users, user],
          });
          setIsAddUserDialogOpen(false);
        }}
      />
    </div>
  );
}

